'use client'

import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { api, type Task, type Plan, type Agent, type Message, type Prompt, type AgentMemory, type AgentContext } from '../api/client'
import { toast } from 'sonner'

// Tasks hooks
export function useTasks(includeAll: boolean = true) {
  return useQuery({
    queryKey: ['tasks', includeAll],
    queryFn: () => api.tasks.list(includeAll),
  })
}

export function useTask(id: string, includeAll: boolean = true) {
  return useQuery({
    queryKey: ['task', id, includeAll],
    queryFn: () => api.tasks.get(id, includeAll),
    enabled: !!id,
  })
}

export function useCreateTask() {
  const queryClient = useQueryClient()
  
  return useMutation({
    mutationFn: api.tasks.create,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tasks'] })
      toast.success('Task created successfully')
    },
    onError: (error: Error) => {
      toast.error(`Failed to create task: ${error.message}`)
    },
  })
}

export function useUpdateTask() {
  const queryClient = useQueryClient()
  
  return useMutation({
    mutationFn: ({ id, data }: { id: string; data: Partial<Task> }) =>
      api.tasks.update(id, data),
    onSuccess: (_, { id }) => {
      queryClient.invalidateQueries({ queryKey: ['tasks'] })
      queryClient.invalidateQueries({ queryKey: ['tasks', id] })
      toast.success('Task updated successfully')
    },
    onError: (error: Error) => {
      toast.error(`Failed to update task: ${error.message}`)
    },
  })
}

export function useDeleteTask() {
  const queryClient = useQueryClient()
  
  return useMutation({
    mutationFn: api.tasks.delete,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['tasks'] })
      toast.success('Task deleted successfully')
    },
    onError: (error: Error) => {
      toast.error(`Failed to delete task: ${error.message}`)
    },
  })
}

